import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import login from "./login.js";
import selectvideos from "./selectVideos.js";
import moveLinkBoxer from "./boxercise.js";
import moveLinkHiit from "./hiit.js";
import moveLinkYoga from "./yoga.js";
import result from "./result.js";

class App extends React.Component {
  render() {
    return (
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route exact path="/" component={login} />
          <Route exact path="/select" component={selectvideos} />
          <Route exact path="/boxercise" component={moveLinkBoxer} />
          <Route exact path="/hiit" component={moveLinkHiit} />
          <Route exact path="/yoga" component={moveLinkYoga} />
          <Route exact path="/result" component={result} />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;
